import {IContainer} from "./interface/i-container";
import {Badge} from "./../button/badge";
import {IButton} from "./../button/interface/i-button";
import {EventEmitter} from "../core/event-emitter";
import {IRenderable} from "../core/interface/i-renderable";
import {AButton} from "./../button/abstract/a-button";
import jquery = require("jquery");

export interface ITab extends IRenderable {
	/**
	 * Evento que será emitido quando a tab for ativada
	 */
	onActive: EventEmitter<any>;
	/**
	 * Evento que será emitido quando a tab for fechada
	 */
	onClose: EventEmitter<any>;
	/**
	 * Remove a aba do tabpanel
	 */
	close(): this;
	/**
	 * Informa qual conteudo a aba deve exibir
	 */
	setContent(container: IContainer<any>): this;
	/**
	 * Obtém o conteúdo
	 */
	getContent(): IContainer<any>;
	/**
	 * Informa o título da aba
	 */
	setTitle(title: string): this;
	/**
	 * Recupera o título da aba
	 */
	getTitle(): string;
	/**
	 * Informa se a aba está visivel, ou não
	 */
	setActive(active: boolean): this;
	/**
	 * Informa se a aba é fechavel
	 */
	setClosable(closable: boolean): this;
}

/**
 * Aba que será inserida no TabPanel
 * 
 * @memberof module:mvcomponents/container
 * 
 * @example
 *
 *  let tabpanel = new TabPanel()
 *      .setSize(12);
 *      .append(new Tab()
 *          .setTitle("Formulário")
 *          .setContent(boxContainer1))
 *      .append(new Tab()
 *          .setTitle("Tab 2")
 *          .setContent(boxContainer2))
 *      .append(net Tab()
 *          setTitle("Tab 3")
 *          .setContent(boxContainer3))
 *
 *  let page:ViewPager = new ViewPager()
 *      .append(tabpanel)
 *      .appendTo("body");
 * 
 */
export class Tab extends AButton implements ITab {
	public onActive: EventEmitter<void> = new EventEmitter<void>();
	public onClose: EventEmitter<void> = new EventEmitter<void>();
	public content: IContainer<any>;
	private title:string;

	constructor(title: string="") {
		super("li", "<a href=\"#\"><span class=\"linkbutton_label_\"></span></a>");
		jquery(this.element).addClass("Tab");
		this.setLabel(title);
	}

	/**
	 * Seta o icone que será exibido pelo botão
	 * @param {string} psrc icone que será exibido
	 * @return {Tab}
	 */
	public setIcon(psrc: string): this {
		if (jquery(this.element).find("span.iconeinput:first").length === 0) {
			jquery(this.element)
				.find("a:first")
				.append(`<span class="iconeinput ${psrc}"></span>`);
		} else {
			var classes = jquery(this.element)
				.find("span.iconeinput")
				.attr("class");
			jquery(this.element)
				.find("span.iconeinput")
				.removeClass()
				.addClass(psrc);
		}
		return this;
	}

	/**
	 * Seta o badge que será exibido pela aba.
	 * @param {Badge} badge badge que será exibido
	 * @return {Tab}
	 */    
	public setBadge(badge:Badge): this{
		if (jquery(this.element).find("span.badge:first").length > 0) {
			jquery(this.element)
				.find("span.badge")
				.remove();
		}
		jquery(this.element)
			.find("a:first")
			.append(jquery(badge.element));
		return this;
	}

	/**
	 * Seta o label que será exibido pela aba.
	 * @param {string} label label que será exibido
	 * @return {Tab}
	 */    
	public setLabel(label: string): this {
		jquery(this.element)
			.find("a:first span.linkbutton_label_:first")
			.text(label);
		this.title = label;	
		return this;
	}

	/**
	 * Fecha a aba.
	 * @return {Tab}
	 */ 
	public close(): this {
		jquery(this.element).remove();
		jquery(this.content.element).remove();
		this.onClose.emit(null);
		return this;
	}

	/**
	 * Seta o conteudo do container da aba.
	 * @return {Tab}
	 */ 
	public setContent(container: IContainer<any>): this {
		this.content = container;
		return this;
	}

	/**
	 * Seta o atributo 'title' da aba.
	 * @return {Tab}
	 */
	public setTooltip(tooltip: string): this {
		if ( tooltip ) {
			jquery(this.element).children("a").attr("title", tooltip);
		} else {
			jquery(this.element).children("a").removeAttr("title");
		}
		return this;
	}

	public getContent(): IContainer<any> {
		return this.content;
	}

	/**
	 * Habilita ou desabilita.
	 * @param {boolean} active label que será exibido
	 * @return {Tab}
	 */    
	public setActive(active: boolean): this {
		if (active) {            
			this.onActive.emit(null).done(()=>{
				jquery(this.element).addClass("active");           
				jquery(this.content.element).addClass("is-visible").removeClass("is-hidden");
			});
		} else {
			jquery(this.element).removeClass("active");
			jquery(this.content.element).addClass("is-hidden").removeClass("is-visible");            
		}
		return this;
	}

	/**
	 * @param title {string}
	 * @return {Tab}
	 */
	public setTitle(title: string): this {
		this.title = title;
		this.setLabel(title);
		return this;
	}
	/**
	 * @return {string}
	 */
	public getTitle(): string {
		return this.title;
	}

	/**
	 * Indica se a aba é fechavel ou não.
	 * @param closable {boolean}
	 * @return {Tab}
	 */
	public setClosable(closable: boolean): this {
		jquery(this.element)
			.find("i.close-handler")
			.remove();
		if (closable) {
			jquery(this.element)
				.children("a")
				.append(jquery("<i class=\"close-handler glyphicon glyphicon-remove\">")
					.click(() => this.close()));
		}
		return this;
	}
}
